import ConfirmDialog from "../../components/ConfirmDialog";

const Logout = (props) => {
  const logout = () => {
    localStorage.clear();
    props.history.push("/auth/login");
  };

  return (
    <ConfirmDialog
      show={true}
      text="Apakah anda yakin akan logout?"
      onNegative={() => window.history.back()}
      onPositive={logout}
    />
  );
};

export default Logout;
